<template>
  <div class="user-profile">
      <div class = "user-profile_user-panel">
          <h1 class="user-profile_username">@{{ user.userName }}</h1>
          <div class = "user-profile_admin-badge" v-if = 'user.isAdmin'>Admin</div>
          <div class = "user-profile_follower-count">
            <strong>Followers : </strong> {{followers}}
          </div>

          <form class = "user-profile_create_twoot" @submit.prevent="createNewTwoot()" :class = "{'--exceeded' : newTwootCharacterCount > 180}">
              <label for="newTwoot"><strong>New Twoot</strong>({{newTwootCharacterCount}}/180)</label>
              <textarea id="newTwoot" rows="4" v-model="newTwootContent"></textarea>

              <div class = "user-profile_create-twoot-type">
                  <label for="newTwootType"><strong>Type : </strong></label>
                  <select id="newTwootType" v-model="newTwootType">
                      <option :value="option.value" v-for= "(option, index) in twootType" :key="index">
                          {{option.name}}
                      </option>
                  </select>
              </div>

              <button>Tweet!</button>
          </form>
      </div>

      <div class = "user-profile_twoots-wrapper">
          <TwootItem 
            v-for = "t in user.twoots" 
            :key = "t.id" 
            :username = "user.userName" 
            :twoot = "t"
            @favourite = "toggleFavourite"
            />
      </div>
  </div>
</template>
 

<script>
import TwootItem from "./TwootItem"
export default {
    name : "UserProfile",
    components : {TwootItem},
    data() {
    return {
        newTwootContent : '',
        newTwootType : 'instant',
        twootType : [
          {value : 'draft', name : 'Draft'},
          {value : 'instant', name : 'Instant Tweet'}
      ],
      followers : 0,
      user :{
          id : 1,
          userName : 'zahoorghani',
          firstName : 'Zahoor',
          lastName : 'Ghani',
          email : 'zahoorghani@gmail.com',
          isAdmin : false,
          twoots : [
            { id : 1, content : 'This is first tweet'},
            {id : 2, content : 'This is second tweet'}
        ]
      }
    }
  },
  watch : {
    followers (newFollowersCount, oldFollowersCount)
    {
      if (oldFollowersCount < newFollowersCount)
      {
        console.log(`${this.user.userName} has gained a follower!`);
      }
    },


  },
  computed: {
      fullName(){
        return `${this.user.firstName} ${this.user.lastName}`;
      },

      newTwootCharacterCount(){
        return this.newTwootContent.length;
      }
    },
    methods : {
      followUser()
      {
        this.followers++;
      },

      toggleFavourite(id)
      {
          console.log(`Your favourite tweet is ${id}`);
      },

      createNewTwoot(){
          if(this.newTwootContent && this.newTwootType != 'draft' && this.newTwootContent.length < 180)
          {
              console.log(this.newTwootContent);
            this.user.twoots.unshift({
            id : this.user.twoots.length + 1,
            content : this.newTwootContent
            })
          }
      }
    },
    
    mounted()
    {
      this.followUser();
    }
  
}
</script>

